html[data-theme='light']{

	//comment content
	--comment-content-body-color:#858597;
	--comment-posted-date-color:#FF6905;

	--comments-scrollbar-color:rgba(192, 192, 192, 0.2);
	--comments-scrollbar-thumb-color:#85859744;

	//comment form
	--comment-send-button-svg-color:#3D5CFF;
	--comment-form-shadow-color:rgb(0 0 0 / 7%);

}
html[data-theme='dark']{

	//comment content
	--comment-content-body-color:#ffffff8a;
	--comment-posted-date-color:#ff8f05;
	
	//comment form
	--comment-send-button-svg-color:#3D5CFF;
	--comment-form-shadow-color:rgb(255 254 254 / 7%);

	--comments-scrollbar-color:rgba(192, 192, 192, 0.2);
	--comments-scrollbar-thumb-color:rgba(255, 255, 255, 0.26);
}



.comments_wrapper{
&.hide{
	height: 0;
}
}
.comments{
	max-height: 250px;
	overflow-y: auto;
	padding-right: 10px;
	&::-webkit-scrollbar{
		width: 5px;
		background-color: var(--comments-scrollbar-color);
	}
	&::-webkit-scrollbar-thumb {
		background: var(--comments-scrollbar-thumb-color); 
		border-radius: 3px;
	  }
	&_no{
		font-family: $Poppins;
		font-style: normal;
		font-weight: 400;
		font-size: 14px;
		line-height: 21px;
	}
}

.comment{
	display: flex;
	padding: 5px 0;
	
	&_content{
		width: 100%;
	}
	&_body{
		margin: 5px 0;
		font-family: $Poppins;
		font-style: normal;
		font-weight: 400;
		font-size: 14px;
		line-height: 18px;
		color: var(--comment-content-body-color);
	}
	&_posted-date{
		font-family: $Poppins;
		font-style: normal;
		font-weight: 400;
		font-size: 12px;
		line-height: 18px;
		margin: 0;
		color:var(--comment-posted-date-color);
	}
	.author{

		&_info{
			display: flex;
			justify-content: space-between;
		}
		&_avatar{
			margin-right: 10px;
			&-img{
				width: 40px;
				height: 40px;
				object-fit: cover;
				border-radius:50%;
			}
		}
		&_name{
			font-family: $Poppins;
			font-style: normal;
			font-weight: 700;
			font-size: 15px;
			line-height: 18px;
			margin: 5px 0;
			color: var(--post-user-name-color);
		}
	}
}


//comment form

.comment_form-wrapper{
	display: flex;
	align-items: center;
	padding-top: 20px;
	
}
.comment_input-wrapper{
	width: 100%;
	padding-right:20px;
}
.send_comment-button{
	&:hover{

	}
	.comment_btn-svg{
		color: var(--comment-send-button-svg-color);
	}
	&:disabled{
		background-color: transparentize($app-blue, $amount: 0.5);
		border-radius: 15px;
		height: 40px;
		cursor:not-allowed;
	}
}


.comments_wrapper{
	transition: 0.3s all cubic-bezier(0.39, 0.575, 0.565, 1);
	//animation: show 1s;
	overflow: hidden;
	height: 0;

	&.show{
		//height: 325px;
		opacity: 1;
	}
	&.hide{
		//height: 0px;
		opacity: 0;
		overflow: hidden;
	}
	
  }
//animations

@keyframes show {
	0%{
		height: 0px;
	}
	100%{
		height: 250px;
	}
}



