
:root{
	--padding-container:50px;
	@media screen and (max-width:1300px)
	{
		--padding-container:35px;
	}
	@media screen and (max-width:1000px)
	{
		--padding-container:20px;
	}
	@media screen and (min-width:1600px)
	{
		--padding-container:0;
	}
}

html[data-theme='light']{

	--box-shadow:-7px -7px 20px 0px #fff9,
	-4px -4px 5px 0px #fff9,
	7px 7px 20px 0px #0002,
	4px 4px 5px 0px #0001,
	inset 0px 0px 0px 0px #fff9,
	inset 0px 0px 0px 0px #0001,
	inset 0px 0px 0px 0px #fff9,
	inset 0px 0px 0px 0px #0001;
}

html[data-theme='dark']{
	--box-shadow:0px 4px 10px var(--form-shadow-color);

}

@mixin mobile{
	@media screen and (max-width:767px)
	{
		@content;
	}
}
@mixin tablet{
	@media (min-width:767px) and (max-width:1100px)
	{
		@content;
	}
}
@mixin desctop{
	@media screen and (min-width:1200px)
	{
		@content;
	}
}
@mixin desctop-big{
	@media screen and (min-width:1600px)
	{
		@content;
	}
}

@mixin container($paddingTopBottom: 0px ){
	padding: $paddingTopBottom var(--padding-container);
	box-sizing: border-box;
	@include desctop-big(){
		width: 1400px;
		margin-left: auto;
		margin-right: auto;
	}
}
@mixin full-width{
	margin: 0 calc(var(--padding-container) * -1);
	padding: 0 var(--padding-container);
}

@mixin box-shadow{
	box-shadow: var(--box-shadow)
}